import React from 'react';
import {
  cerebrum,
  gitlens,
  hcissAcad,
  hcissWeb,
  kings4,
  nishpress,
  scuudu,
} from '../../../assets';
import './portfolio.css';

const Portfolio = () => {
  const projects = [
    {
      id: 1,
      name: 'Scuddu Events',
      description: 'An Application for Event creation and confirmation',
      label: 'web',
      img: scuudu,
      url: 'https://scuudu-events.netlify.app/#/',
    },
    {
      id: 2,
      name: 'Gitlens',
      description:
        'A React  Application  to View your github profile and repositories',
      label: 'node',
      img: gitlens,
      url: 'https://kings-gitlens-v2.netlify.app/',
    },
    {
      id: 3,
      name: 'Hciss Academy',
      description: 'A landing page for a Blockchain Academy',
      label: 'react',
      img: hcissAcad,
      url: 'https://hciss-academy.netlify.app/',
    },
    {
      id: 4,
      name: 'Hciss Portal',
      description:
        'A fullstack Application built with laravel framework for a Blockchain cybersecurity company',
      label: 'laravel',
      img: hcissWeb,
      url: 'https://hciss.org/',
    },
    {
      id: 5,
      name: 'Cerebrum',
      description: 'An Online Learning Application',
      label: 'react',
      img: cerebrum,
      url: 'https://umucere.netlify.app/',
    },
    {
      id: 6,
      name: 'NishPress',
      description: 'An Online Magazine  built  with wordpress',
      label: 'react',
      img: nishpress,
      url: 'http://nish.press/',
    },
  ];

  const [activeTab, setActiveTab] = React.useState('all');

  const All = () => {
    return projects.map(project => (
      <section
        className="col-md-4 project-card mb-5 mb-sm-2"
        data-aos="fade-up"
        data-aos-duration="2000"
      >
        <img src={project.img} alt={project.name} />
        <div className="port-card-content ">
          <h4>{project.name}</h4>
          <p>{project.description}</p>
          <a href={project.url}>View Project</a>
        </div>
      </section>
    ));
  };

  const ReactProjects = () => {
    return projects
      .filter(project => project.label === 'react')
      .map(filteredProject => {
        return (
          <section
            className="col-md-4 project-card"
            data-aos="fade-up"
            data-aos-duration="2000"
          >
            <img src={filteredProject.img} alt={filteredProject.name} />
            <div className="port-card-content ">
              <h4>{filteredProject.name}</h4>
              <p>{filteredProject.description}</p>
              <a href={filteredProject.url}>View Project</a>
            </div>
          </section>
        );
      });
  };
  const Node = () => {
    return projects
      .filter(project => project.label === 'node')
      .map(filteredProject => {
        return (
          <section
            className="col-md-4 project-card"
            data-aos="fade-up"
            data-aos-duration="2000"
          >
            <img src={filteredProject.img} alt={filteredProject.name} />
            <div
              className="port-card-content "
              data-aos="fade-up"
              data-aos-duration="2000"
            >
              <h4>{filteredProject.name}</h4>
              <p>{filteredProject.description}</p>
              <a href={filteredProject.url}>View Project</a>
            </div>
          </section>
        );
      });
  };
  const Laravel = () => {
    return projects
      .filter(project => project.label === 'laravel')
      .map(filteredProject => {
        return (
          <section
            className="col-md-4 project-card"
            data-aos="fade-up"
            data-aos-duration="2000"
          >
            <img src={filteredProject.img} alt={filteredProject.name} />
            <div className="port-card-content ">
              <h4>{filteredProject.name}</h4>
              <p>{filteredProject.description}</p>
              <a href={filteredProject.url}>View Project</a>
            </div>
          </section>
        );
      });
  };
  return (
    <section className="port-container">
      <div className="contact-header mb-sm-5 d-flex justify-content-center align-items-center flex-column">
        <div>
          <p>
            Have a <span className="text-primary">look</span> at my projects
            team contributions
          </p>
        </div>
        <div>
          <h1>Portfolio</h1>
        </div>
        <div className="contact-divider bg-primary"></div>
      </div>
      <section className="mt-4">
        <header>
          <ul className="tab-header">
            <li className="tab-item" onClick={() => setActiveTab('all')}>
              All
            </li>
            <li className="tab-item" onClick={() => setActiveTab('react')}>
              React
            </li>
            <li className="tab-item" onClick={() => setActiveTab('node')}>
              Node
            </li>
            <li className="tab-item" onClick={() => setActiveTab('laravel')}>
              Laravel
            </li>
          </ul>
        </header>
        <section className="container row project-card-container">
          {activeTab === 'all' && <All />}
          {activeTab === 'react' && <ReactProjects />}
          {activeTab === 'node' && <Node />}
          {activeTab === 'laravel' && <Laravel />}
        </section>
      </section>
    </section>
  );
};

export default Portfolio;
