import React from 'react';
import {
  cerebrum,
  gitlens,
  hcissAcad,
  hcissWeb,
  nishpress,
  scuudu,
} from '../../../assets';
import './projects.css';

const Projects = () => {
  const projects = [
    {
      id: 1,
      name: 'Scuddu Events',
      title: 'Host and Share your Events with Attendees',
      description:
        'This is a startup project that I am currently working on in collaboration with the Scuddu team. Built entirely for mobile first, with all other design concepts taken into account. We hope to shift the application to the blockchain area in the near future.',
      label: [
        { title: 'React', variant: 'bg-primary' },
        { title: 'Redux', variant: 'bg-secondary' },
        { title: ' React Hooks', variant: 'bg-danger' },
        { title: ' Formik', variant: 'bg-warning' },
        { title: ' Axios', variant: 'bg-success' },
      ],
      img: scuudu,
      url: 'https://scuudu-events.netlify.app/#/',
    },
    {
      id: 2,
      name: 'Gitlens',
      title: 'Search Github Users and their Repos',
      description:
        'This is a personal devlopment project, where I got to use the Github Api, maximizing the power of context API and React Hooks to create an application where you can search and checkout github users as well as their repos',
      label: [
        { title: 'React', variant: 'bg-primary' },
        { title: 'Context API', variant: 'bg-secondary' },
        { title: ' React Hooks', variant: 'bg-danger' },
      ],
      img: gitlens,
      url: 'https://kings-gitlens-v2.netlify.app/',
    },
    {
      id: 3,
      name: 'Hciss Academy',
      title: 'Cybersecurity and Blcokchain Academy',
      description: 'A landing page for a Blockchain Academy',
      label: [
        { title: 'React', variant: 'bg-primary' },
        { title: 'React Hooks', variant: 'bg-danger' },
        { title: ' Framer Motion', variant: 'bg-warning' },
      ],
      img: hcissAcad,
      url: 'https://hciss-academy.netlify.app/',
    },
    {
      id: 4,
      name: 'HCISS',
      title: 'A companys Website For HCISS',
      description:
        'A full-stack application created with the Laravel framework for a Blockchain cybersecurity firm, complete with a blogging platform.',
      label: [
        { title: 'Laravel', variant: 'bg-primary' },
        { title: 'Bootstrap', variant: 'bg-danger' },
        { title: 'CSS', variant: 'bg-warning' },
      ],
      img: hcissWeb,
      url: 'https://hciss.org/',
    },
    {
      id: 5,
      name: 'Cerebrum',
      title: 'Knowledge for Everyone By Everyone',
      description:
        'An Online Learning Application created by an outstanding team of 8 during my internship at Genesys Tech Hub. We proceeded through the complete software development process, adopting Agile methodology and design thinking all the way through to project completion. We used Node in the backend and React in the frontend, and I was one of the frontend developers who brought the apps UI to life.',
      label: [
        { title: 'React', variant: 'bg-primary' },
        { title: 'Node', variant: 'bg-danger' },
        { title: 'Javascript', variant: 'bg-warning' },
      ],
      img: cerebrum,
      url: 'https://umucere.netlify.app/',
    },
    {
      id: 6,
      name: 'NishPress',
      title: 'Natural Hair Magazine',
      description:
        'Maximizing the power of Wordpress Framework, I was able to bring the online magazine Nishpress to life taking into consideration quality UI and SEO, as well as easy navigation for users',
      label: [
        { title: 'Wordpress', variant: 'bg-primary' },
        { title: 'SEO', variant: 'bg-danger' },
      ],
      img: nishpress,
      url: 'http://nish.press/',
    },
  ];

  const All = () => {
    return projects.map(project => (
      <section className="row mb-5 gx-5">
        <section
          className="project-content col-lg-4 col-md-12 col-sm-12 "
          data-aos="fade-up"
          data-aos-duration="2000"
        >
          <div className="project-name">{project.name}</div>
          <div className="project-title">
            <h2>{project.title}</h2>
          </div>
          <div className="project-line"></div>
          <div className="project-desc">
            <p>{project.description}</p>
          </div>

          <div className="project-tags">
            {project.label.map(tag => (
              <div className={`badge ${tag.variant}`}>
                <small>{tag.title} &nbsp;</small>
              </div>
            ))}
          </div>
          <div className="call-to-action">
            <a href={project.url} target="_blank">
              <button
                treatas="link"
                className="btn btn-dark 
            text-white px-5 py-3 mt-3 d-flex align-items-center 
            justify-content-between"
              >
                View Project
              </button>
            </a>
          </div>
        </section>
        <section
          className="project-avatar col-sm-12 col-lg-8 col-md-12 order-first order-md-2"
          data-aos="fade-up"
          data-aos-duration="2000"
        >
          <img src={project.img} alt="all" />
        </section>
      </section>
    ));
  };

  return (
    <section className="project-container container" id="featured">
      <div className="mb-5">
        <h1 className="text-center">Featured Projects</h1>
        <div className="project-line" style={{ margin: 'auto' }}></div>
      </div>

      <All />
      <div className="mt-5 py-5">
        <h1 className="text-center">Want Us to Work Together?</h1>
        <div className="project-line" style={{ margin: 'auto' }}></div>
        <div className="call-to-action cta">
          <a href="mailto:ajaezokingsley@gmail.com" target="_blank">
            <button
              treatas="link"
              className="btn btn-dark 
            text-white px-5 py-3 mt-3 d-flex align-items-center 
            justify-content-between"
            >
              Contact Me
            </button>
          </a>
        </div>
      </div>
    </section>
  );
};

export default Projects;
